body,
html {
  margin: 0px;
  padding: 0px;
  position: fixed;
  background: black;
}

.homePage {
    background-color: black; /* Set background color */
    width: 100vw; /* Full viewport width */
    height: 100vh; /* Full viewport height */
    position: relative;
  }

.buymeacoffee {
  text-decoration: none;
  position: fixed;
  right: 2rem;
  bottom: 2rem;
  background: #FFDD00;
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 50%;
  transition: all 0.1s linear;
  box-shadow: 0.2rem 0.2rem 0.5rem rgba(0, 0, 0, 0.2);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.buymeacoffee:hover {
  width: 4rem;
  height: 4rem;
  line-height: 4rem;
}

.loaderOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1000; /* High z-index to ensure it's above other content */
  display: flex;
  justify-content: center;
  align-items: center;
  /* Add other styling for background, etc., as needed */
}

.loaderOverlay.hidden {
  display: none;
}
